<template>
  <div ref="rootRef">
      <slot></slot>
  </div>
</template>

<script>
import useScroll from './use-scroll'
import { ref } from 'vue'

export default {
  name: 'scroll',
  props: {
    click: {
      type: Boolean,
      default: true
    },
    probeType: {
      type: Number,
      default: 0
    }
  },
  emits: ['scroll'],
  setup (porps, { emit }) {
    const rootRef = ref(null)
    console.log(porps.probeType)
    const scroll = useScroll(rootRef, porps, emit)

    return {
      rootRef,
      scroll
    }
  }
}
</script>

